          <div class="info-list">
            <div class="info-item " v-for="(item,key) in info" :key="key"
                 @mouseover="selectStyle (key)  "
                 :class="{'active':isActive[key]}"
                 @mouseout="outStyle(key)" >
              <div class="info-item1">{{item.projectName}}</div>
              <div class="info-item2">
                <ul>
                  <li><span>计划融资：</span> <span style="color: #008fff;font-size: 20px;">{{item.income}}万元</span></li>
                  <li><span>融资行业：</span><span>{{item.industry}}</span><span>融资阶段：</span><span>{{item.projecrPhase}}</span></li>
