<script>
    import { regionData,CodeToText } from 'element-china-area-data'
    export default {
        name: "IndusFinanc",
        data(){
            return {
                value:'',
                total:100,
                isActive:{
                    0:false,
                    1:false,
                    2:false,
                    3:false,
                    4:false,
                    5:false,
                    6:false,
                    7:false,
                    8:false,
                },
                info:'',
                input:'',
                checkList:[],
                checkList1:[],
                options: regionData,
                selectedOptions:'',
                baseForm:{
                    city:'',
                    district:''
                }
            }
        },
        created(){
            this.getInfo(1);
        },
        methods:{
            //所属城市
            handleChange (value) {
                var arr = [];
                value.forEach((item,index)=>{
                    arr.push(CodeToText[item])
                })
                this.baseForm.city = arr;
                if (arr[1] =='市辖区'){
                    this.baseForm.city = arr[0];
                }else {
                    this.baseForm.city = arr[1];
                }
                this.baseForm.district = arr[2];

            },
                handleCurrentChange(val){
                 console.log(val)
                    this.getInfo(val);
                },
                //页面改变
                handleSizeChange(val) {
                    console.log(`每页 ${val} 条`);
                },
            selectStyle(i){
                this.isActive[i] = true;
            },
            outStyle(i) {
                this.isActive[i] = false;
            },
            //获取项目融资信息
            getInfo(i){
                var pageNum = i || 1;
                var that = this;
                that.$axios.get('/Financing/findFinancingByCondition',{
                    params:{
                        pagenum:pageNum,
                        pageSize:12
                    }
                }).then((res)=>{
                    console.log(res)
                    if (res.data.code == 0){
                        that.info = res.data.data.list;
                        that.total = res.data.data.total;
                    }
                })
            },
        },


    }
</script>

